'use client';
import { useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
export default function LoginPage(){
  const [email,setEmail]=useState(''); const [pwd,setPwd]=useState(''); const [busy,setBusy]=useState(false); const [err,setErr]=useState<string|null>(null);
  const router = useRouter(); const sp = useSearchParams(); const next = sp.get('next') || '/dashboard';
  async function onSubmit(e:React.FormEvent){ e.preventDefault(); setBusy(true); setErr(null);
    try{ const r=await fetch('/api/auth/login',{method:'POST',headers:{'Content-Type':'application/json'},body:JSON.stringify({email,password:pwd})}); if(!r.ok) throw new Error('Login failed'); router.push(next); }
    catch(e:any){ setErr(e?.message||'Login error'); } finally{ setBusy(false); } }
  return (<div style={{maxWidth:520, margin:'10vh auto'}} className="card"><h2>Sign in</h2><p className="muted">Demo-only authentication (any email/password)</p>{err&&<div style={{color:'#ef4444',marginBottom:8}}>{err}</div>}
    <form onSubmit={onSubmit} className="row" style={{flexDirection:'column',alignItems:'stretch'}}>
      <input className="input" placeholder="Email" value={email} onChange={e=>setEmail(e.target.value)} />
      <input className="input" type="password" placeholder="Password" value={pwd} onChange={e=>setPwd(e.target.value)} />
      <button className="btn btn-primary" type="submit" disabled={busy}>{busy?'Signing inâ€¦':'Continue'}</button>
    </form></div>);}
